import { useTagged } from 'react-tagged-state';
import { ReactElement } from 'react';
import heightState from '../states/heightState';
import minMax from '../utils/minMax';

const HeightInput = (): ReactElement => {
    const height = useTagged<number>(heightState);

    return (
        <label>
            Height:
            <input
                key={height}
                defaultValue={height}
                type="number"
                onBlur={(event: { target: HTMLInputElement }) => heightState(minMax(1, +(event.target.value || 0), 99))}
                min={8}
                max={99}
            />
        </label>
    );
};

export default HeightInput;
