import endTimeState from '../states/endTime';
import gameOverState from '../states/gameOverState';
import boardState, { BoardState } from '../states/boardState';
import flagBombs from '../utils/flagBombs';
import copyBoard from '../utils/copyBoard';

const win = (): void => {
    endTimeState(Date.now());
    gameOverState('win');
    boardState((board: BoardState): BoardState => flagBombs(copyBoard(board)));
};

export default win;
