import { useTagged } from 'react-tagged-state';
import { ReactElement } from 'react';
import widthState from '../states/widthState';
import minMax from '../utils/minMax';

const WidthInput = (): ReactElement => {
    const width = useTagged<number>(widthState);

    return (
        <label>
            Width:
            <input
                key={width}
                defaultValue={width}
                type="number"
                onBlur={(event: { target: HTMLInputElement }) => widthState(minMax(8, +(event.target.value || 0), 99))}
                min={8}
                max={99}
            />
        </label>
    );
};

export default WidthInput;
