import getOpenNearStatus from '../utils/getOpenNearStatus';
import boardState, { BoardState, Cell } from '../states/boardState';
import openNearCells from '../utils/openNearCells';
import copyBoard from '../utils/copyBoard';
import win from './win';
import lose from './lose';

const openNear = (cell: Cell): void => {
    const openNearStatus = getOpenNearStatus(boardState(), cell);

    if (openNearStatus === 0) {
        return;
    }

    if (openNearStatus === -1) {
        lose();

        return;
    }

    boardState((board: BoardState): BoardState => openNearCells(copyBoard(board), cell));

    if (boardState().cells === 0) {
        win();
    }
};

export default openNear;
