import { ReactElement, useLayoutEffect, MouseEvent } from 'react';
import { useTagged } from 'react-tagged-state';
import open from '../actions/open';
import openNear from '../actions/openNear';
import flag from '../actions/flag';
import start from '../actions/start';
import boardState, { BoardState, Cell } from '../states/boardState';
import gameOverState, { GameOver } from '../states/gameOverState';
import widthState from '../states/widthState';
import heightState from '../states/heightState';
import generateBoardState from '../utils/generateBoardState';

const CELL_SIZE = 20;

const HALF_CELL_SIZE = CELL_SIZE / 2;

const BOARD_ID = 'BOARD_ID';

const CELL_COLOR: Record<number, string> = {
    1: 'blue',
    2: 'green',
    3: 'red',
    4: 'purple',
    5: 'maroon',
    6: 'turquoise',
    7: 'black',
    8: 'gray'
};

const getEventPoint = (event: MouseEvent) => {
    const rect = document.getElementById(BOARD_ID)?.getBoundingClientRect();

    if (!rect) {
        return [0, 0];
    }

    return [Math.floor((event.clientY - rect.top) / CELL_SIZE), Math.floor((event.clientX - rect.left) / CELL_SIZE)];
};

const renderEmptyBoard = (ctx: CanvasRenderingContext2D, { rows, cols }: { rows: number; cols: number }) => {
    ctx.fillStyle = 'black';
    ctx.fillRect(0, 0, cols * CELL_SIZE, rows * CELL_SIZE);
};

const renderCell = (ctx: CanvasRenderingContext2D, { cell }: { cell: Cell }) => {
    const x = cell.col * CELL_SIZE;
    const y = cell.row * CELL_SIZE;

    ctx.fillStyle = cell.isOpened ? 'aliceblue' : 'lightgray';
    ctx.fillRect(x, y, CELL_SIZE - 1, CELL_SIZE - 1);
    ctx.font =
        "12px -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif";
    ctx.textAlign = 'center';
    ctx.textBaseline = 'middle';

    if (!cell.isOpened) {
        if (cell.isFlag) {
            ctx.fillText('🚩', x + HALF_CELL_SIZE - 1, y + HALF_CELL_SIZE);

            return;
        }

        return;
    }

    if (cell.isBomb) {
        if (cell.isFlag) {
            ctx.fillText('✅', x + HALF_CELL_SIZE - 1, y + HALF_CELL_SIZE);

            return;
        }

        ctx.fillText('💣', x + HALF_CELL_SIZE - 1, y + HALF_CELL_SIZE);

        return;
    }

    if (!cell.bombCount) {
        return;
    }

    if (CELL_COLOR[cell.bombCount]) {
        ctx.fillStyle = CELL_COLOR[cell.bombCount];
    }

    ctx.fillText(`${cell.bombCount}`, x + HALF_CELL_SIZE - 1, y + HALF_CELL_SIZE);
};

const renderBoard = (ctx: CanvasRenderingContext2D, { board }: { board: BoardState['board'] }) => {
    let row = 0;
    let col = 0;
    const rows = board.length;
    const cols = board[0].length;

    renderEmptyBoard(ctx, { rows, cols });

    for (; row < rows; ++row) {
        for (col = 0; col < cols; ++col) {
            renderCell(ctx, { cell: board[row][col] });
        }
    }
};

const Board = (): ReactElement => {
    const board = useTagged<BoardState>(boardState);
    const gameOver = useTagged<GameOver>(gameOverState);
    const width = useTagged<number>(widthState);
    const height = useTagged<number>(heightState);
    const boardBoard = board ? board.board : generateBoardState(width, height, 0).board;
    const boardWidth = boardBoard[0].length * CELL_SIZE;
    const boardHeight = boardBoard.length * CELL_SIZE;

    useLayoutEffect(() => {
        const ctx = (document.getElementById(BOARD_ID) as HTMLCanvasElement).getContext('2d');

        if (ctx) {
            renderBoard(ctx, { board: boardBoard });
        }
    }, [boardBoard]);

    return (
        <canvas
            id={BOARD_ID}
            style={{
                width: boardWidth,
                height: boardHeight,
                userSelect: 'none',
                cursor: 'pointer',
                pointerEvents: gameOver ? 'none' : undefined
            }}
            data-is-over={!!gameOver}
            width={boardWidth}
            height={boardHeight}
            onClick={(event) => {
                const [row, col] = getEventPoint(event);

                if (board) {
                    open(boardBoard[row][col]);

                    return;
                }

                start(row, col);
            }}
            onDoubleClick={
                board
                    ? (event: MouseEvent) => {
                          const [row, col] = getEventPoint(event);

                          openNear(boardBoard[row][col]);
                      }
                    : undefined
            }
            onContextMenu={
                board
                    ? (event: MouseEvent) => {
                          event.preventDefault();

                          const [row, col] = getEventPoint(event);

                          flag(boardBoard[row][col]);
                      }
                    : undefined
            }
        />
    );
};

export default Board;
